import React, { Component } from 'react';
import { Form } from 'antd';
import { TableForm } from 'components/common';

@Form.create({ name: 'formTableQualifiedExamGraduation' })
class TableQualifiedExamGraduation extends Component {
  constructor(props) {
    super(props);
    this.refTable = React.createRef();
    this.state = {};
  }

  getTableData = () => {
    return this.refTable.current.getTableData();
  }

  createColumns = () => {
    const columns1 = [
      {
        title: 'Mã sinh viên',
        dataIndex: 'studentCode',
        key: 'studentCode',
      },
      {
        title: 'Họ và tên',
        dataIndex: 'fullName',
        key: 'fullName',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dob',
        key: 'dob',
      },
      {
        title: 'Giới tính',
        dataIndex: 'genderText',
        key: 'genderText',
      },
      {
        title: 'Nơi sinh',
        dataIndex: 'pobText',
        key: 'pobText',
      },
      {
        title: 'Chuyên ngành',
        dataIndex: 'studyFieldLabel',
        key: 'studyFieldLabel',
      },
      {
        title: 'Tên lớp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'DXLTN4',
        dataIndex: 'totalCourseIn4',
        key: 'totalCourseIn4',
      },
      {
        title: 'DXLTN10',
        dataIndex: 'totalCourse',
        key: 'totalCourse',
      },
      {
        title: 'Xếp hạng',
        dataIndex: 'graduateClassificationLabel',
        key: 'graduateClassificationLabel',
      },
      {
        title: 'Năm xét',
        dataIndex: 'yearEdu',
        key: 'yearEdu',
      },
      {
        title: 'Đợt xét',
        dataIndex: 'sessionNo',
        key: 'sessionNo',
      },
    ];
    const columns2 = [
      {
        title: 'Ghi chú',
        dataIndex: 'reason',
        key: 'reason',
      },
    ];
    return columns1.concat(columns2);
  };

  render() {
    const {
      reload,
      rowKey,
      data,
      handleSelectedRows,
      form,
    } = this.props;
    const columns = this.createColumns();
    return (
      <div>
        <TableForm
          reload={reload}
          data={data}
          handleSelectedRows={handleSelectedRows}
          rowKey={rowKey}
          columns={columns}
          form={form}
          ref={this.refTable}
        />
      </div>
    );
  }
}

export default TableQualifiedExamGraduation;



