import React, { Component } from 'react';
import { Row, Form, Input } from 'antd';
import { BtnSave, BtnLock, BtnExport } from 'components/common/button';
import { TableForm } from 'components/common';

@Form.create({ name: 'formTableQualifiedDoThesis' })
class TableQualifiedDoThesis extends Component {
  constructor(props) {
    super(props);
    this.refTable = React.createRef();
    this.state = {};
  }

  getTableData = () => {
    return this.refTable.current.getTableData();
  }

  createColumns = () => {
    const columns1 = [
      {
        title: 'Mã sinh viên',
        dataIndex: 'studentCode',
        key: 'studentCode',
      },
      {
        title: 'Họ và tên',
        dataIndex: 'fullName',
        key: 'fullName',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dob',
        key: 'dob',
      },
      {
        title: 'Giới tính',
        dataIndex: 'genderText',
        key: 'genderText',
      },
      {
        title: 'Nơi sinh',
        dataIndex: 'pobText',
        key: 'pobText',
      },
      {
        title: 'Chuyên ngành',
        dataIndex: 'studyFieldLabel',
        key: 'studyFieldLabel',
      },
      {
        title: 'Tên lớp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'DXLTN4',
        dataIndex: 'totalCourseIn4',
        key: 'totalCourseIn4',
      },
      {
        title: 'DXLTN10',
        dataIndex: 'totalCourse',
        key: 'totalCourse',
      },
      {
        title: 'Xếp hạng',
        dataIndex: 'graduateClassificationLabel',
        key: 'graduateClassificationLabel',
      },
      {
        title: 'Năm xét',
        dataIndex: 'yearEdu',
        key: 'yearEdu',
      },
      {
        title: 'Đợt xét',
        dataIndex: 'sessionNo',
        key: 'sessionNo',
      },
    ];
    const columns2 = [
      {
        title: 'Ghi chú',
        dataIndex: 'reason',
        key: 'reason',
        render: (_text, record) => {
          const { getFieldDecorator } = this.props.form;
          return (
            <Form.Item>
              {getFieldDecorator(`reason-${record.autoKey}`, {
                initialValue: record.reason,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng điền ghi chú!',
                  },
                ],
              })(
                <Input />
              )}
            </Form.Item>
          );
        },
      },
    ];
    return columns1.concat(columns2);
  };

  render() {
    const {
      reload,
      rowKey,
      data,
      handleSelectedRows,
      form,
      createUpdateListStudentCheckGraduate,
      createUpdateListStudentCheckNoGraduate,
      exportPDF,
      exportExcel,
      disableJoinBtn,
      disableExport
    } = this.props;
    const columns = this.createColumns();
    return (
      <div>
        <Row>
          <div className="btn-group" style={{ float: 'right' }}>
            <BtnSave
              title="Xét tốt nghiệp"
              onClick={createUpdateListStudentCheckGraduate}
              disabled={disableJoinBtn}
            />
            <BtnLock
              icon="exclamation-circle"
              title="Chưa đủ điều kiện"
              onClick={createUpdateListStudentCheckNoGraduate}
              disabled={disableJoinBtn}
            />
            <BtnExport
              title="Xuất Excel"
              onClick={exportExcel}
              disabled={disableJoinBtn || disableExport}
            />
            <BtnExport
              title="Xuất PDF"
              onClick={exportPDF}
              disabled={disableJoinBtn || disableExport}
            />
          </div>
        </Row>
        <TableForm
          reload={reload}
          data={data}
          handleSelectedRows={handleSelectedRows}
          rowKey={rowKey}
          columns={columns}
          form={form}
          ref={this.refTable}
        />
      </div>
    );
  }
}

export default TableQualifiedDoThesis;



