import React, { Component } from 'react';
import { Row, Col, Tabs, Spin, message } from 'antd';
import {
  createUpdateListStudentCheckGraduate,
  createUpdateListStudentCheckNoGraduate,
} from 'services/StudentToGraduate';
import {
  exportExcelStudentCheckGraduate,
  exportPdfStudentCheckGraduate,
} from 'services/classCreditEnrollment';
import {
  getListDataValid
} from 'utils/formUtil';
import StudentCheckGraduateTable from './StudentCheckGraduateTable';
import StudentCheckNoGraduateTable from './StudentCheckNoGraduateTable';
import './style.scss';

class TabInfoStudentPreparingGraduation extends Component {
  constructor(props) {
    super(props);
    this.refFormForProject = React.createRef();
    this.refFormRawForProject = React.createRef();
    this.refFormForGraduateTest = React.createRef();
    this.refFormRawForGraduateTest = React.createRef();
    this.state = {
      loading: false,
      reloadForProject: 0,
      reloadForGraduateTest: 0,
      selectedRows: [],
      rowKey: "autoKey",
    };
  }

  handleSelectedRows = (selectedRows) => {
    this.setState({
      selectedRows,
    });
  }

  createUpdateListStudentCheckGraduate = () => {
    try {
      const { selectedRows, rowKey } = this.state;
      const { onJoinGraduateTestSuccess, queryParams } = this.props;
      if (selectedRows && selectedRows.length) {
        const listFieldName = [];
        this.setState({
          loading: true,
        });
        getListDataValid(rowKey, listFieldName, this.refFormForProject, this.refFormRawForProject, selectedRows)
          .then(
            async (listSelectedItem) => {
              const formData = {
                yearEduId: queryParams.yearEduId,
                sessionNo: queryParams.sessionNo,
                studentCheckGraduates: [...listSelectedItem],
              }

              try {
                await createUpdateListStudentCheckGraduate(formData);
                message.success('Lưu sinh viên ĐỦ điều kiện tốt nghiệp thành công.');
                this.setState({
                  selectedRows: [],
                  loading: false,
                },
                  () => onJoinGraduateTestSuccess(),
                );
              } catch (error) {
                console.log(error);
              } finally {
                this.setState({
                  loading: false,
                });
              }
            }
          )
          .catch(
            () => {
              this.setState({
                loading: false,
              });
            }
          )
      }
    } catch (error) {
      console.log(error);
    }
  }

  createUpdateListStudentCheckNoGraduate = () => {
    try {
      const { selectedRows, rowKey } = this.state;
      const { onJoinGraduateTestSuccess, queryParams } = this.props;
      if (selectedRows && selectedRows.length) {
        const listFieldName = [
          'reason',
        ];
        this.setState({
          loading: true,
        });
        getListDataValid(rowKey, listFieldName, this.refFormForProject, this.refFormRawForProject, selectedRows)
          .then(
            async (listSelectedItem) => {
              const formData = {
                yearEduId: queryParams.yearEduId,
                sessionNo: queryParams.sessionNo,
                studentCheckGraduates: [...listSelectedItem],
              }

              try {
                await createUpdateListStudentCheckNoGraduate(formData);
                message.success('Lưu sinh viên CHƯA đủ điều kiện tốt nghiệp thành công.');
                this.setState({
                  selectedRows: [],
                  loading: false,
                },
                  () => onJoinGraduateTestSuccess(),
                );
              } catch (error) {
                console.log(error);
              } finally {
                this.setState({
                  loading: false,
                });
              }
            }
          )
          .catch(
            () => {
              this.setState({
                loading: false,
              });
            }
          )
      }
    } catch (error) {
      console.log(error);
    }
  }

  handleExportPDFForProject = async () => {
    try {
      this.setState({
        loading: true,
      });
      const { queryParams } = this.props;
      await exportPdfStudentCheckGraduate(queryParams);
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  }

  handleExportExcelForProject = async () => {
    try {
      this.setState({
        loading: true,
      });
      const { queryParams } = this.props;
      await exportExcelStudentCheckGraduate(queryParams);
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  }

  render() {
    const { TabPane } = Tabs;
    const {
      dataStudentForProject,
      dataStudentForGraduateTest,
      queryParams
    } = this.props;
    const {
      reloadForProject,
      reloadForGraduateTest,
      rowKey,
      loading,
    } = this.state;
    const disableJoinBtn = !(dataStudentForProject && dataStudentForProject.length);
    const disableExport = !(!!queryParams.trainTypeId && !!queryParams.facultyId && !!queryParams.courseId && !!queryParams.yearEduId && !!queryParams.sessionNo);
    return (
      <Spin spinning={loading}>
        <Row>
          <Col span={24}>
            <Tabs defaultActiveKey="1">
              <TabPane tab="Đủ điều kiện tốt nghiệp ra trường" key="1">
                <StudentCheckGraduateTable
                  reload={reloadForProject}
                  data={dataStudentForProject}
                  ref={this.refFormForProject}
                  handleSelectedRows={this.handleSelectedRows}
                  rowKey={rowKey}
                  wrappedComponentRef={(form) => {
                    this.refFormRawForProject = form;
                  }}
                  createUpdateListStudentCheckGraduate={this.createUpdateListStudentCheckGraduate}
                  createUpdateListStudentCheckNoGraduate={this.createUpdateListStudentCheckNoGraduate}
                  exportPDF={this.handleExportPDFForProject}
                  exportExcel={this.handleExportExcelForProject}
                  disableJoinBtn={disableJoinBtn}
                  disableExport={disableExport}
                />
              </TabPane>
              <TabPane tab="Chưa đủ điều kiện tốt nghiệp" key="2">
                <StudentCheckNoGraduateTable
                  reload={reloadForGraduateTest}
                  data={dataStudentForGraduateTest}
                  ref={this.refFormForGraduateTest}
                  rowKey={rowKey}
                  wrappedComponentRef={(form) => {
                    this.refFormRawForGraduateTest = form;
                  }}
                />
              </TabPane>
            </Tabs>
          </Col>
        </Row>
      </Spin>
    );
  }
}

export default TabInfoStudentPreparingGraduation;



