import React, { Component } from 'react';
import { Col, Form, Row, Modal, DatePicker, Spin, Select, Input } from 'antd';
import { BtnSearch, BtnClear, BtnExport } from 'components/common/button';
import FilterPathEducate from 'components/common/input/FilterPathEducate/index';
import { SelectAjax } from 'components/common';
import { requiredRule } from 'utils/validateRules';
import { updateQueryParams, getQueryParams } from 'utils/urlUtil';
import moment from 'moment';

import { getYearEducations } from 'services/yearEducationService';
import { getResgisterSessionSelect } from 'services/registerSession';
import {
  exportExcelStudentGraduateSummary,
  exportPdfStudentGraduateSummary,
} from 'services/StudentToGraduate';

import './style.scss';

const { RangePicker } = DatePicker;
const { Option } = Select;

@Form.create({ name: 'formTeacher' })
class FormFilterReward extends Component {
  constructor(props) {
    super(props);
    this.titleForExportPDF = "Xuất PDF theo năm";
    this.titleForExportExcel = "Xuất Excel theo năm";
    this.state = {
      yearToExport: [moment(), moment().add(1, 'y')],
      showModal: false,
      titleModal: this.titleForExportPDF,
      serviceModal: null,
      loading: false,
    };
  }

  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  onChangeYearModal = value => {
    this.setState({ yearToExport: value });
  };


  getResgisterSessionSelectCustom = async params => {
    const res = await getResgisterSessionSelect({ ...params, fullFormat: true });
    return res;
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        console.log(err)
        return;
      }
      updateQueryParams(values);
    });
  };

  handleReset = () => {
    this.props.form.resetFields();
    updateQueryParams();
  };

  handleExportPDFForProject = async () => {
    try {
      this.setState({
        loading: true,
      });
      const { yearToExport } = this.state;
      const fromYear = yearToExport[0].year();
      const toYear = yearToExport[1].year();
      await exportPdfStudentGraduateSummary({ fromYear, toYear });
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  }

  handleExportExcelForProject = async () => {
    try {
      this.setState({
        loading: true,
      });
      const { yearToExport } = this.state;
      const fromYear = yearToExport[0].year();
      const toYear = yearToExport[1].year();
      await exportExcelStudentGraduateSummary({ fromYear, toYear });
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  }

  openModalExcel = () => {
    this.setState({
      showModal: true,
      titleModal: this.titleForExportExcel,
      serviceModal: this.handleExportExcelForProject,
    });
  }

  openModalPDF = () => {
    this.setState({
      showModal: true,
      titleModal: this.titleForExportPDF,
      serviceModal: this.handleExportPDFForProject,
    });
  }

  hideModal = () => {
    this.setState({
      showModal: false,
      titleModal: this.titleForExportExcel,
      serviceModal: null,
      yearToExport: [moment(), moment().add(1, 'y')],
    });
  }
  render() {
    const { showModal, yearToExport, titleModal, serviceModal, loading } = this.state;
    const {
      getFieldDecorator,
      // getFieldValue,
      // setFieldsValue
    } = this.props.form;
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Hệ!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khóa',
          },
        ],
      },
    };
    // const trainTypeId = getFieldValue('trainTypeId');
    return (
      <Spin spinning={loading}>
        <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
          <Row gutter={24}>
            <FilterPathEducate form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
          </Row>
          <Row gutter={24}>
            <Col span={5}>
              <Form.Item label="Năm học">
                {getFieldDecorator('yearEduId', {
                  rules: [requiredRule],
                })(
                  <SelectAjax
                    service={getYearEducations}
                    setLabel={yearEdu => `${yearEdu.fromYear}-${yearEdu.toYear}`}
                    typeValue="int"
                  />
                )}
              </Form.Item>
            </Col>
            {/* <Col span={5}>
              <Form.Item label="Đợt đăng ký">
                {getFieldDecorator('sessionNo', {
                  rules: [requiredRule],
                })(
                  <SelectAjax
                    setFieldsValue={setFieldsValue}
                    service={this.getResgisterSessionSelectCustom}
                    params={{ trainTypeId, fullformat: true }}
                    allowGetData={!!trainTypeId}
                    keyValue="value"
                  />
                )}
              </Form.Item>
            </Col> */}
            <Col span={5}>
              <Form.Item label="Đợt">
                {getFieldDecorator('sessionNo', {
                  rules: [requiredRule],
                })(
                  <Select >
                    <Option value="1">1</Option>
                    <Option value="2">2</Option>
                    <Option value="3">3</Option>
                    <Option value="4">4</Option>
                  </Select>
                )}
              </Form.Item>
            </Col>
            <Col span={5}>
              <Form.Item label="MSSV">
                {getFieldDecorator('studentCode', {
                })(
                  <Input />
                )}
              </Form.Item>
            </Col>
            <Col span={5}>
              <Form.Item label="Họ và tên">
                {getFieldDecorator('studentName', {
                })(
                  <Input />
                )}
              </Form.Item>
            </Col>
            <Col span={4}>
              <Form.Item label="Sắp xếp">
                {getFieldDecorator('orderBy', {
                })(
                  <Select >
                    <Option value="studentCode">MSSV</Option>
                    <Option value="studentName">Họ và tên</Option>
                  </Select>
                )}
              </Form.Item>
            </Col>
          </Row>
          <Row>
            <Col>
              <div className="btn-group" style={{ float: 'right' }}>
                <BtnSearch htmlType="submit" />
                <BtnClear onClick={this.handleReset} />
                <BtnExport title="Xuất báo cáo" onClick={this.openModalExcel} />
                <BtnExport title="In báo cáo" onClick={this.openModalPDF} />
              </div>
            </Col>
          </Row>
        </Form>
        <Modal
          title={titleModal}
          visible={showModal}
          onOk={this.hideModal}
          onCancel={this.hideModal}
          footer={null}
        >
          <Row>
            <Col span={16}>
              <RangePicker
                placeholder={['Từ Năm', 'Đến Năm']}
                mode={['year', 'year']}
                value={yearToExport}
                format="YYYY"
                onChange={this.onChangeYearModal}
                onPanelChange={this.onChangeYearModal}
              />
            </Col>
            <Col span={4}>
              <BtnExport title="Xuất" onClick={serviceModal} />
            </Col>
          </Row>
        </Modal>
      </Spin>
    );
  }
}

export default FormFilterReward;



